.reviews__wrap {
  background-color: $pastel;
  margin-bottom: 20px;

  @include tablet {
    margin-bottom: 40px;
  }

  @include desktop {
    margin-bottom: 90px;
  }
}

.reviews {
  display: flex;
  flex-direction: column;
  padding: 7px 15px 15px;

  @include tablet {
    padding: 20px 52px 30px;
  }

  @include desktop {
    padding: 40px 85px;
  }
}

.reviews__top {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 15px;

  @include tablet {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 20px;
  }

  @include desktop {
    margin-bottom: 30px;
  }
}

.reviews__title {
  text-align: center;
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;

  @include tablet {
    text-align: left;
    font-weight: 600;
    font-size: 32px;
    line-height: calc(39px / 32px);
  }
}

.reviews__raiting {
  display: flex;
  margin-bottom: 5px;

  @include tablet {
    margin-bottom: 0px;
  }
}

.reviews__form-open-btn {
  width: fit-content;
  text-decoration: underline;
  font-weight: 500;
  font-size: 15px;
  line-height: calc(18px / 15px);
  color: $main-blue;

  @include tablet {
    font-weight: 600;
    font-size: 20px;
    line-height: calc(24px / 20px);
    color: $black;
  }
}

.reviews__form-open-btn:hover,
.reviews__form-open-btn:focus {
  color: $dark-blue;
}

.reviews__list {
  margin-bottom: 15px;
  background-color: $pastel;

  @include tablet {
    margin-bottom: 30px;
  }

  @include desktop {
    margin-bottom: 40px;
  }
}

.reviews__item {
  margin-bottom: 10px;
  padding: 10px 15px;
  background-color: $white;
  border: 1px solid $light-grey;
  border-radius: 5px;

  @include tablet {
    position: relative;
    padding: 15px;
    margin-bottom: 15px;
  }

  @include desktop {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 20px;
    padding: 15px 35px 25px 15px;
  }
}

.reviews__item:last-child {
  margin-bottom: 0px;
}

.reviews__item-left {
  @include desktop {
    display: flex;
    flex-direction: column;
    gap: 15px;
    width: 216px;
  }
}

.reviews__item-right {
  @include tablet {
    width: 635px;
  }

  @include desktop {
    width: 785px;
  }
}

.reviews__item-raiting {
  display: flex;
  margin-bottom: 4px;

  @include tablet {
    position: absolute;
    right: 15;
    margin-bottom: 0px;
  }

  @include desktop {
    position: unset;
  }
}

.reviews__item-info {
  display: flex;
  gap: 10px;
  align-items: center;
  margin-bottom: 10px;

  @include tablet {
    margin-bottom: 15px;
  }

  @include desktop {
    margin-bottom: 0px;
  }
}

.reviews__item-letter-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35px;
  height: 35px;
  background-color: $dark-blue;
  border-radius: 50%;

  @include tablet {
    width: 45px;
    height: 45px;
  }
}

.reviews__item-letter {
  color: $white;
  font-weight: 500;
  font-size: 20px;
  line-height: calc(24px / 20px);
}

.reviews__item-name {
  font-weight: 600;
}

.reviews__item-date {
  font-size: 14px;
  line-height: calc(17px / 14px);

  @include tablet {
    font-size: 15px;
    line-height: calc(18px / 15px);
  }
}

.reviews__item-title {
  margin-bottom: 10px;
  font-weight: 600;
  line-height: 1.3;

  @include tablet {
    margin-bottom: 5px;
    font-size: 15px;
  }

  @include desktop {
    margin-bottom: 10px;
    font-size: 18px;
  }
}

.reviews__item-text {
  font-size: 14px;
  line-height: 1.3;

  @include tablet {
    font-size: 15px;
  }

  @include desktop {
    font-size: 16px;
  }
}

.reviews__show-more-btn {
  width: fit-content;
  margin: 0px auto;
  text-decoration: underline;
  font-weight: 600;
  font-size: 15px;
  line-height: calc(18px / 15px);

  @include tablet {
    font-size: 20px;
    line-height: calc(24px / 20px);
  }
}

.reviews__show-more-btn:hover,
.reviews__show-more-btn:focus {
  color: $dark-blue;
}

.reviews__form {
  margin-bottom: 23px;
  opacity: 1;
  transition: opacity 0.5s, height 0.5s, margin 0.5s;

  @include tablet {
    margin-bottom: 30px;
  }

  @include desktop {
    margin-bottom: 50px;
  }
}

.o0 {
  opacity: 0;
}

.reviews__form-inputs-wrap {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 10px;

  @include tablet {
    margin-bottom: 15px;
    gap: 15px;
  }

  @include desktop {
    margin-bottom: 20px;
    gap: 20px;
  }
}

.reviews__form-input-wrap {
  display: flex;
  flex-direction: column;
  gap: 10px;

  @include tablet {
    flex-direction: row;
    gap: 15px;
  }

  @include desktop {
    gap: 20px;
  }
}

.reviews__form-label {
  line-height: 1.3;

  @include tablet {
    margin-bottom: 10px;
    font-size: 20px;
  }
}

.reviews__form-raiting {
  display: flex;
  margin-bottom: 10px;

  @include tablet {
    margin-bottom: 20px;
  }
}

.reviews__form-input {
  width: 100%;
  padding: 8px 15px;
  line-height: 1.3;
  font-size: 15px;
  border: 1px solid $light-grey;
  border-radius: 5px;

  @include tablet {
    width: 400px;
    padding: 17px 25px;
    font-size: 16px;
  }
}

.reviews__form-textarea {
  padding: 12px 15px;
  line-height: 1.3;
  border: 1px solid $light-grey;
  border-radius: 5px;
  resize: none;

  @include tablet {
    height: 101px;
    padding: 20px 25px;
  }

  @include desktop {
    height: 189px;
  }
}

.reviews__form-submit-btn-wrap {
  display: flex;
  justify-content: flex-end;
}

.reviews__form-submit-btn {
  padding: 8px 20px;
  color: $white;
  font-weight: 600;
  font-size: 15px;
  line-height: 1.3;
  background-color: $main-blue;
  border: 1px solid $light-grey;
  border-radius: 5px;

  @include tablet {
    padding: 7px 20px;
    font-size: 20px;
  }

  @include desktop {
    padding: 9.5px 30px;
    font-size: 24px;
  }
}

.reviews__form-submit-btn:hover,
.reviews__form-submit-btn:focus {
  background-color: $dark-blue;
}

.w820p {
  @include tablet {
    width: 100%;
  }

  @include desktop {
    width: 820px;
  }
}

.h0 {
  height: 0px;
  margin-bottom: 0px;
  pointer-events: none;
}

.h490 {
  height: 318px;

  @include tablet {
    height: 378px;
  }

  @include desktop {
    height: 490px;
  }
}
